import {useEffect, useRef} from 'react'

const useUpdate = (fn: () => void,dependency: any[])=> {
  const count = useRef(0)
  useEffect(()=>{
    count.current+=1
  }  )

  useEffect(()=>{
    if(count.current > 1){
      fn();
    }
  }, [fn,dependency]) // 当tags发生变化储存数据, 一定要做到不可以变数据
}


export { useUpdate }
